
// Fonts
@import url("https://fonts.googleapis.com/css?family=Raleway:300,400,600");

// Variables
@import "variables";

// Bootstrap
@import '~bootstrap/scss/bootstrap';

// Font Awesome
@import '~@fortawesome/fontawesome-free-webfonts/scss/fa-brands';
@import '~@fortawesome/fontawesome-free-webfonts/scss/fa-solid';
@import '~@fortawesome/fontawesome-free-webfonts/scss/fa-regular';
@import '~@fortawesome/fontawesome-free-webfonts/scss/fontawesome';


@import '~slick-carousel/slick/slick-theme';

nav {
  font-size: 1.2rem;
}

.logo {
  height: 40px;

  @include media-breakpoint-up (sm) {
    height: 50px;
  }
}

.loading {
  position: fixed;
  top: 0;
  left: 0;
  height: 3px;
  background-color: $primary;
  width: 0%;
  z-index: 1100;
  transition: width .2s;
}

.image-field {
  &:hover {
    -webkit-box-shadow: 0 0 0 0.2rem rgba(108, 117, 125, 0.5);
    box-shadow: 0 0 0 0.2rem rgba(108, 117, 125, 0.5);
  }

  -webkit-transition: box-shadow 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  transition: box-shadow 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  max-height: 135px;
  cursor: pointer;
}

.svg-outline-secondary {
  path {
    fill: $secondary;
    stroke: $secondary;
    -webkit-transition: fill 0.15s ease-in-out, stroke 0.15s ease-in-out;
    transition: fill 0.15s ease-in-out, stroke 0.15s ease-in-out;
  }

  &:hover path {
    fill: $white;
    stroke: $white;
  }
}

.active-image {
  -webkit-box-shadow: 0 0 0 0.2rem rgba(108, 117, 125, 0.5);
  box-shadow: 0 0 0 0.2rem rgba(108, 117, 125, 0.5);
}

div.is-invalid {
  border-radius: 5px;
  border: 1px solid #dc3545;

  & ~ .invalid-feedback {
    display: block;
  }
}

.bigger {
  font-size: 1.25rem;
}
.bit-bigger {
  font-size: 1.1rem;
}
.slideshow {
  overflow: hidden
}

.slick-list {
  max-height: 250px
}

.slick-slide {
  text-align: center;
  display: inline-block;
}

.img-slideshow {
  max-height: 250px;
  max-width: 100%
}

.slick-dots {
  position: static
}
.slick-dots li button:before {
  font-size: 15px
}

.markdown {
  img {
    max-width: 100%;
    height: auto;
  }
}



